import { WOO_API } from "../woocommerce";
import { ORDER_GET, ORDER_STORE, ORDER_ERROR } from "./actionTypes";
import { cartRemoveAllItemsAction } from "./Cart";

export function orderStoreAction(data) {
  return { type: ORDER_STORE, data };
}

export function orderGetAction(data) {
  return { type: ORDER_GET, data };
}

export function orderErrorAction(error) {
  return { type: ORDER_ERROR, error };
}

export function orderStore(dispatch, customer, cart) {
  const line_items = [];
  cart.items.forEach(item => {
    line_items.push({ product_id: item.produto.id, quantity: item.quantidade });
  });
  const data = {
    payment_method: "bacs",
    payment_method_title: "Direct Bank Transfer",
    set_paid: false,
    shipping: customer.shipping,

    line_items: line_items,
    shipping_lines: [
      {
        method_id: "local_pickup",
        method_title: "Retirar no Local"
      }
    ]
  };

  WOO_API.post("orders", data)
    .then(response => {
      dispatch(orderStoreAction(response.data));
      dispatch(cartRemoveAllItemsAction());
    })
    .catch(error => {
      dispatch(orderErrorAction(error.response));
    });
}

export function orderGet(dispatch, order_id) {
  WOO_API.get("orders/" + { order_id })
    .then(response => {
      dispatch(orderGetAction(response.data));
    })
    .catch(error => {
      dispatch(orderErrorAction(error.response));
    });
}
