import React from "react";
import { useDispatch } from "react-redux";
import { orderStore } from "../../actions/Order";
import { green } from "@material-ui/core/colors";
import {
  Box,
  Button,
  FormControl,
  RadioGroup,
  FormControlLabel,
  Radio,
  Grid
} from "@material-ui/core";

export default function Pagamento(props) {
  const { cart, gateways, customer } = props;
  const tmpgateways = [
    { id: 1, title: "Boleto" },
    { id: 2, title: "Cartão de Crédito" }
  ];
  const dispatch = useDispatch();
  const [value, setValue] = React.useState("");

  const handleChange = event => {
    setValue(event.target.value);
  };

  const finalizarCompra = () => {
    orderStore(dispatch, customer, cart);
  };

  return (
    <Box p={2}>
      <h3>Opções de pagamento</h3>
      <Grid container>
        <Grid item xs={12}>
          <Button onClick={props.prevClick}>Voltar</Button>
        </Grid>
        <Grid item xs={12}>
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="pagamento"
              name="pagamento"
              value={value}
              onChange={handleChange}
            >
              {tmpgateways.map(gw => (
                <FormControlLabel
                  value={gw.id}
                  control={<Radio />}
                  label={gw.title}
                  key={gw.id}
                />
              ))}
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item xs={12} style={{ marginTop: 20 }}>
          <Button
            variant="contained"
            color="primary"
            style={{ backgroundColor: green[500], width: "100%" }}
            onClick={finalizarCompra}
          >
            FINALIZAR COMPRA
          </Button>
        </Grid>
      </Grid>
    </Box>
  );
}
